.App {
  text-align: center;
  padding: 2rem;
  margin: 1rem;
  border: 5px dashed black;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
  color: black;
  line-height: 2em;
}

.App-intro {
  font-size: large;
  margin-top: 2em;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}
